import React, { Component } from 'react';
import './App.css';

class Fib extends Component {

render() {
  var r = (x) => (Math.round(parseFloat(x)*100) *0.01).toFixed(2);
  var p = (x) => parseFloat(x);
  return (
    <div className="">
      <div className='cell small title'>  Fibonacci  </div> 
      <div className='cell small resistance3'>  ${   r(p(this.props.state.close) + (p(this.props.state.range))) } </div>  
      <div className='cell small resistance2'> ${   r(p(this.props.state.close) + (p(this.props.state.range))*0.618) }</div>
      <div className='cell small resistance1'> ${   r(p(this.props.state.close) + (p(this.props.state.range))*0.382) } </div>
      <div className='cell small pivot'> ${  r(p(this.props.state.classicPP)) } </div>
      <div className='cell small support1'> ${   r(p(this.props.state.close) - (p(this.props.state.range))*0.382) } </div>
      <div className='cell small support2'>  ${   r(p(this.props.state.close) - (p(this.props.state.range))*0.618) }</div>
      <div className='cell small support3'> ${   r(p(this.props.state.close) - (p(this.props.state.range))) } </div> 
       
    </div>
  );
}
}

export default Fib;
