import React, { Component } from 'react';
import './App.css';

class Demark extends Component {

render() {
  var r = (x) => (Math.round(parseFloat(x)*100) *0.01).toFixed(2);
  var p = (x) => parseFloat(x);
  return (
    <div className="">
      <div className='cell small title'> DeMark </div> 
      <div className='cell small resistance3'> --</div>  
      <div className='cell small resistance2'> -- </div>
      <div className='cell small resistance1'> ${  r(p(this.props.state.x)/2-p(this.props.state.low)) } </div>
      <div className='cell small pivot'> ${  r(p(this.props.state.x)/4) } </div>
      <div className='cell small support1'> ${  r(p(this.props.state.x)/2-p(this.props.state.high)) } </div>
      <div className='cell small support2'> -- </div>
      <div className='cell small support3'> -- </div> 
    </div>
  );
}
}

export default Demark;
