import React, { Component } from 'react';
import './App.css';

class Classic extends Component {

render() {
  var r = (x) => (Math.round(parseFloat(x)*100) *0.01).toFixed(2);
  var p = (x) => parseFloat(x);
  return (
    <div className="">
    <div className='cell title'>  Classic  </div> 
    <div className='cell resistance3'> ${   r(p(this.props.state.classicPP) + 2*(p(this.props.state.high) - p(this.props.state.low))) } </div>  
    <div className='cell resistance2'> ${   r(p(this.props.state.classicPP) +p(this.props.state.high) - p(this.props.state.low)) } </div>
    <div className='cell resistance1'> ${   r(2*p(this.props.state.classicPP) - p(this.props.state.low)) } </div>
    <div className='cell pivot'> ${  r(p(this.props.state.classicPP)) }</div>
    <div className='cell support1'>  ${   r(2*p(this.props.state.classicPP) - p(this.props.state.high)) }</div>
    <div className='cell support2'>  ${   r(p(this.props.state.classicPP) - p(this.props.state.high) + p(this.props.state.low)) } </div>
    <div className='cell support3'>  ${   r(p(this.props.state.classicPP) - 2*(p(this.props.state.high) - p(this.props.state.low))) }</div> 
    </div>
  );
}
}

export default Classic;
