import React, { Component } from 'react';
import './App.css';

class Camarilla extends Component {

render() {
  var r = (x) => (Math.round(parseFloat(x)*100) *0.01).toFixed(2);
  var p = (x) => parseFloat(x);
  return (
    <div className="">
      <div className='cell title'> Camarilla  </div> 
      <div className='cell resistance3'> ${   r(p(this.props.state.close) + (p(this.props.state.range))*1.1/4) }</div>  
      <div className='cell resistance2'> ${   r(p(this.props.state.close) + (p(this.props.state.range))*1.1/6) }</div>
      <div className='cell resistance1'> ${   r(p(this.props.state.close) + (p(this.props.state.range))*1.1/12) } </div>
      <div className='cell pivot'> -- </div>
      <div className='cell support1'> ${   r(p(this.props.state.close) - (p(this.props.state.range))*1.1/12) } </div>
      <div className='cell support2'> ${  r(p(this.props.state.close) - (p(this.props.state.range))*1.1/6) } </div>
      <div className='cell support3'> ${   r(p(this.props.state.close) - (p(this.props.state.range))*1.1/4) } </div> 
    </div>
  );
}
}

export default Camarilla;
